import Vue from 'vue'
import Vuex from 'vuex' // глобальное хранилище, доступно во всех компонентах

import axios from 'axios';
import {
  API_BASE_URL
} from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    productLoading: false,
    productLoadingFailed: false,
    amountMessage: '',
    orderInfo: null,





  },
  mutations: {
   updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, {
      productId,
      amount
    }) { // увеличиваем количество товара в корзине
      const item = state.cartProducts.find(item => item.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) { // удаляем товар из корзины
      state.cartProducts = state.cartProducts.filter(item => item.productId != productId)
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;

    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) { // синхронизируем  состояние хранилища и данные с сервера
      state.cartProducts = state.cartProductsData.map(item => {
        return {
          productId: item.product.id,
          amount: item.quantity
        }
      })
    },



  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map(item => {
        const product = state.cartProductsData.find(p => p.product.id === item.productId)
          .product;
        return {
          ...item,
          product: { // подмешиваем картинку к товару
            ...product,
            image: product.image.file.url
          }
        }
      })
    },
    cartTotalPrice(state, getters) { // общая стоимость
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item
        .amount) + acc, 0);
    },
    quantity(state) {
    return state.cartProducts.reduce((acc, item) => item.amount + acc, 0);

    },

  },
  actions: {

    loadOrderInfo(context, orderId) {// загружаем данные о заказе
      return axios
      .get(API_BASE_URL + 'api/orders/' + orderId, {
       params: {
         userAccessKey: context.state.userAccessKey
      }
     })
     .then(response => {
       context.commit('updateOrderInfo', response.data);
     });
    },
    // обращать к api в мутации нельзя, так как мутации сихронные, поэтому создаем действие
    loadCart(context) { // получаем корзину
      this.state.productLoading = true;
      this.state.productLoadingFailed = false;
      return (new Promise(resolve => setTimeout(resolve, 2000)))
        .then(() => {
          return axios.get(API_BASE_URL + 'api/baskets', {
              params: {

                userAccessKey: context.state
                  .userAccessKey // подмешиваем уникальный ключ к свойствам
              }
            })
            .then(response => {
              if (!context.state
                .userAccessKey
              ) { // если в глобальном хранилище ничего нет, то сохраняем  ключ в локалстораж
                localStorage.setItem('userAccessKey', response.data.user.accessKey)
                context.commit('updateUserAccessKey', response.data.user
                  .accessKey) // получаем уникальный ключ при  первом запросе
              }
              context.commit('updateCartProductsData', response.data.items);
              context.commit('syncCartProducts');
            })
            .catch(() => (this.state.productLoadingFailed = true))
            .then(() => (this.state.productLoading = false));
        })
    },

    addProductToCart(context, { // добавляем товар на сервер
      productId,
      amount
    }) {
      return (new Promise(resolve => setTimeout(resolve, 2000)))
        .then(() => {
          return axios.post(API_BASE_URL + 'api/baskets/products', {
              productId: productId,
              quantity: amount,
            }, {
              params: {
                userAccessKey: context.state.userAccessKey
              }

            })
            .then(response => {
              context.commit('updateCartProductsData', response.data.items);
              context.commit('syncCartProducts');
            })
        })
    },
    updateCartProductAmount(context, {
      productId,
      amount
    }) {
      context.commit('updateCartProductAmount', {
        productId,
        amount
      });
      if (amount < 1) {
        return;
      }
      return axios.put(API_BASE_URL +
        'api/baskets/products', { // отправка на сервер изменение количества товара
          quantity: amount,
          productId: productId
        }, {
          params: {
            userAccessKey: context.state.userAccessKey
          }

        })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);

        })
        .catch(() => {
          context.commit('syncCartProducts')
        })

    },
    deleteCartProduct(context, { // удалить товар из корзины
      productId,

    }) {

      return axios.delete(API_BASE_URL + 'api/baskets/products', {
        params: {
          userAccessKey: context.state.userAccessKey
        },
         data: {
        productId }

        })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })

    }

  }

});
