<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }"> Корзина </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> {{ productQuantity }} товара</span>
    </div>

    <section class="cart" >
      <div v-if="productsLoading" id="fountainTextG">
          <div id="fountainTextG_1" class="fountainTextG">L</div>
          <div id="fountainTextG_2" class="fountainTextG">o</div>
          <div id="fountainTextG_3" class="fountainTextG">a</div>
          <div id="fountainTextG_4" class="fountainTextG">d</div>
          <div id="fountainTextG_5" class="fountainTextG">i</div>
          <div id="fountainTextG_6" class="fountainTextG">n</div>
          <div id="fountainTextG_7" class="fountainTextG">g</div>
        </div>
        <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              v-model="formData.name"
              placeholder="Введите ваше полное имя"
              type="text"
              title="ФИО"
              :error="formError.name"
            />
            <BaseFormText
              v-model="formData.address"
              placeholder="Введите ваш адрес"
              type="text"
              title="Адрес доставки"
              :error="formError.address"
            />
            <BaseFormText
              v-model="formData.phone"
              placeholder="Введите ваш телефон"
              type="tel"
              title="Телефон"
              :error="formError.phone"
            />
            <BaseFormText
              v-model="formData.email"
              placeholder="Введи ваш Email"
              type="email"
              title="Email"
              :error="formError.email"
            />
            <BaseFormTextarea
              title="Комментарий к заказу"
              :error="formError.comments"
              v-model="formData.comment"
              placeholder="Ваши пожелания"
            />
            <div class="cart__options">
              <h3 class="cart__title">Доставка</h3>
              <ul class="cart__options options">
                <li class="options__item">
                  <label class="options__label">
                    <input
                      class="options__radio sr-only"
                      type="radio"
                      name="delivery"
                      value="0"
                      checked=""
                    />
                    <span class="options__value"> Самовывоз <b>бесплатно</b> </span>
                  </label>
                </li>
                <li class="options__item">
                  <label class="options__label">
                    <input
                      class="options__radio sr-only"
                      type="radio"
                      name="delivery"
                      value="500"
                    />
                    <span class="options__value"> Курьером <b>500 ₽</b> </span>
                  </label>
                </li>
              </ul>

              <h3 class="cart__title">Оплата</h3>
              <ul class="cart__options options">
                <li class="options__item">
                  <label class="options__label">
                    <input class="options__radio sr-only" type="radio" name="pay" value="card" />
                    <span class="options__value"> Картой при получении </span>
                  </label>
                </li>
                <li class="options__item">
                  <label class="options__label">
                    <input class="options__radio sr-only" type="radio" name="pay" value="cash" />
                    <span class="options__value"> Наличными при получении </span>
                  </label>
                </li>
              </ul>
            </div>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <CartBlock v-for="item in products" :key="item.productId" :item="item"/>
          </ul>

           <CartTotal :total-price ="totalPrice" :product-quantity ="productQuantity"/>

          <button class="cart__button button button--primery" type="submit">Оформить заказ</button>
        </div>
        <div class="cart__error form__error-block" v-if ="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p> {{ formErrorMessage }}</p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormFailed from "@/components/BaseFormField.vue";
import BaseFormText from "@/components/BaseFormText.vue";
import BaseFormTextarea from "@/components/BaseFormTextarea.vue";
import CartBlock from "@/components/CartBlock.vue";
import { API_BASE_URL } from "@/config";
import numberFormat from "@/helpers.js/numberFormat";
import CartTotal from "@/components/CartTotal.vue"
import axios from 'axios';
import { mapGetters } from "vuex";
export default {
  filters: { numberFormat },
  components: { BaseFormFailed, BaseFormText, BaseFormTextarea, CartBlock, CartTotal },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      productsLoading: false
    };
  },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
      productQuantity: "quantity",
    }),


  },
  methods: {


    order() {  // отправляем форму

      this.productsLoading = true
      this.formError = {},
      this.formErrorMessage = ''
      return new Promise((resolve) => setTimeout(resolve, 2000)).then(() => {
      axios
     .post(API_BASE_URL + 'api/orders', {
       ...this.formData
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey
        }

     })
     .then(response => {
      this.$store.commit('resetCart');
     this.$store.commit('updateOrderInfo', response.data); // сохраняем информацию о заказе в глобальном хранилище
     this.$router.push({name: 'orderInfo', params: {id: response.data.id}}) // перенаправить пользоватпеля на страницу заказа с идентификатором  заказа
     })
    .then(() => {
      this.formData = {}
     })

     .catch(error => {
      this.formError = error.response.data.error.request || {}
      this.formErrorMessage = error.response.data.error.message
     })
     .then(() => (this.productsLoading = false
     ))
    })

    }
  },

};
</script>
