<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> 152 товара </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :color-id.sync="filterColorId"
        @reset-page="page = 1"
      />
      <section class="catalog">
        <div v-if="productsLoading" id="fountainTextG">
          <div id="fountainTextG_1" class="fountainTextG">L</div>
          <div id="fountainTextG_2" class="fountainTextG">o</div>
          <div id="fountainTextG_3" class="fountainTextG">a</div>
          <div id="fountainTextG_4" class="fountainTextG">d</div>
          <div id="fountainTextG_5" class="fountainTextG">i</div>
          <div id="fountainTextG_6" class="fountainTextG">n</div>
          <div id="fountainTextG_7" class="fountainTextG">g</div>
        </div>
        <div v-if="productsLoadingFailed" class="catalog__loading">
          <img class="preloader__image" src="../img/smile.jpg" />
          <p class="catalog__eror">ERROR</p>

          <button class="catalog__button-error" @click.prevent="loadProducts">Попробовать еще раз</button>
        </div>
        <ProductList :products="products" />
        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>
<script>
import ProductList from "@/components/ProductList.vue";
import ProductItem from "@/components/ProductItem.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import { API_BASE_URL } from "@/config";
import axios from "axios";
export default {
  components: { ProductList, ProductItem, BasePagination, ProductFilter },

  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: "",

      page: 1,
      productsPerPage: 3,
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => {
            return {
              ...product,
              image: product.image.file.url,
            };
          })
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(API_BASE_URL + 'api/products', {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColorId,
            },
          })
          .then((responce) => (this.productsData = responce.data))
          .catch(() => (this.productsLoadingFailed = true))
          .then(() => (this.productsLoading = false));
      }, 5000);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
