<template>
  <div>
<ProductHeader/>

<router-view/>
<ProductFooter/>

</div>
</template>

<script>
import ProductHeader from './components/ProductHeader.vue';
import ProductFooter from './components/ProductFooter.vue';
import { mapActions, mapMutations } from 'vuex'; //проксирование действия в методы компонента
export default {
components: {ProductHeader, ProductFooter},
created() {
  //this.$store.dispatch('loadCart') вызываем действие шлобального хранилища
  const userAccessKey = localStorage.getItem('userAccessKey') // получаем ключ из локалстораж
  if(userAccessKey) {
    this.updateUserAccessKey(userAccessKey); // передаем в мутацию ключ и сохраняем в глобальном хранилище
  }
  this.loadCart();

},
methods: { // подмешиваем дейсвие в методы
  ...mapActions(['loadCart']),
  ...mapMutations(['updateUserAccessKey'])

}
}
</script>
