            кратковременное (до 30 минут) погружение в воду на глубину не более 1 метра.
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import gotoPage from "@/helpers.js/gotoPage";
import numberFormat from "@/helpers.js/numberFormat";
import axios from "axios";
import ProductColor from '@/components/ProductColor.vue'
import { API_BASE_URL } from "@/config";
import { mapActions } from "vuex";
export default {
  components: {ProductColor},
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingFailed: false,
      productAdded: false,
      productAddSending: false,
    };
  },
  computed: {


    product() {

     return this.productData;

    },
    category() {

      return this.productData.category;
    },
  },
  filters: {
    numberFormat,
  },
  methods: {
    ...mapActions(["addProductToCart"]),// добавляем товары в корзину
    gotoPage,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart({ productId: this.product.id, amount: this.productAmount }) // при вызове действия из хранилища возвраща=ается промис
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });

    },
    increase() {
      // увеличиваем количество на единицу
      this.productAmount += 1;
    },

    decrease() {
      // уменьшаем количество товара
      if (this.productAmount > 0) {
        this.productAmount -= 1;
      }
    },

    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      return new Promise((resolve) => setTimeout(resolve, 2000)).then(() => {
        axios
          .get(API_BASE_URL + "api/products/" + this.$route.params.id)
          .then((response) => (this.productData = response.data))
          .catch(() => (this.productLoadingFailed = true))
          .then(() => (this.productLoading = false));
      });
    },
  },
  watch: {
    "$route.params.id": {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>
