<template>
  <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b> {{ productQuantity }}</b> {{ formatQuanteded }} на сумму <b>{{  totalPrice | numberFormat  }} ₽</b></p>
          </div>
</template>

<script>
import numberFormat from "@/helpers.js/numberFormat";
import wordFormat from "@/helpers.js/wordFormat"
export default {
  filters: { numberFormat },
  props:['totalPrice', 'productQuantity'],
  computed: {
    formatQuanteded () {
      return wordFormat(this.productQuantity, ['товар', 'товара', 'товаров'])

    }
  }

}
</script>
